* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  border-radius: 0.5px;
  background: url(./images/star.svg) no-repeat center;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:first-of-type,
.stars--2 .stars__star:nth-of-type(-n+2),
.stars--3 .stars__star:nth-of-type(-n+3),
.stars--4 .stars__star:nth-of-type(-n+4),
.stars--5 .stars__star:nth-of-type(n) {
  background: url(./images/star-active.svg) no-repeat center;
}
